<template>
  <div class="login_container">
    <div class="login_box">
      <!--  -->
      <div class="avatar_box">
        <img src="../assets/img/jizz414vlztmn.jpg" alt="" />
      </div>
      <!--  -->
      <el-form
        ref="loginFormRef"
        :model="loginForm"
        :rules="loginFormRules"
        label-width="0px"
        class="login_form"
      >
        <!--  -->
        <el-form-item prop="username">
          <el-input
            v-model="loginForm.username"
            prefix-icon="el-icon-s-custom"
          ></el-input>
        </el-form-item>
        <!--  -->
        <el-form-item prop="password">
          <el-input
            v-model="loginForm.password"
            prefix-icon="el-icon-search"
            type="password"
          ></el-input>
        </el-form-item>
        <!--  -->
        <el-form-item class="btns">
          <el-button type="primary" @click="login" style="margin-left: 200px"
            ></el-button
          >
          <el-button type="info" @click="resetLoginForm"></el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      // 
      loginForm: {
        username: "admin",
        password: "123456",
      },
      // 
      loginFormRules: {
        // 
        username: [
          {
            required: true,
            message: "",
            trigger: "blur",
          },
          {
            min: 3,
            max: 10,
            message: "310",
            trigger: "blur",
          },
        ],
        // 
        password: [
          {
            require: true,
            message: "",
            trigger: "blur",
          },
          {
            min: 6,
            max: 15,
            message: "615",
            trigger: "blur",
          },
        ],
      },
    };
  },
  methods: {
    // 
    resetLoginForm() {
      // console.log(this)
      this.$refs.loginFormRef.resetFields();
    },
    login() {
      this.$refs.loginFormRef.validate(async (valid) => {
        if (!valid) return;
        const { data: res } = await this.$http.post("login", this.loginForm);
        //  console.log(res)
        if (res.meta.status !== 200) return this.$message.error("");
        this.$message.success("");
        // 1.  token, sessionStorage 
        // 1.1 API
        // 1.2 token tokensessionStorage 
        res.data.img = "https://i03piccdn.sogoucdn.com/1f1dac102a365433"
        this.$store.dispatch('addID',res.data)
        window.sessionStorage.setItem("token", "woshihhuitailang");
        this.$store.commit("addCookie","woshihhuitailang");
        // 2.  /home
        this.$router.push("/home");
      });
    },
  },
};
</script>

<style scoped>
.login_container {
  height: 906px;
  width: 100%;
  background-color: skyblue;
}

.login_box {
  width: 450px;
  height: 300px;
  background-color: aquamarine;
  border-radius: 3px;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

.avatar_box {
  height: 130px;
  width: 130px;
  border: 1px solid #eee;
  border-radius: 50%;
  padding: 10px;
  box-shadow: 0 0 10px #ddd;
  position: absolute;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: #fff;
}
.avatar_box > img {
  width: 100%;
  height: 100%;
  border-radius: 50%;
  background-color: #eee;
}

.login_form {
  margin-top: 100px;
  bottom: 0;
  width: 100%;
  padding: 0 20px;
  box-sizing: border-box;
}

.btn {
  display: flex;
  justify-content: flex-end;
}
</style>
